.paraImage {
    max-width: 970px; /* Maximum width of the image */
    max-height: 850px; /* Maximum height of the image */
    width: 96vw;      /* Image width will be 96% of the viewport width */
    height: auto;     /* Maintain aspect ratio, adjust height automatically */
    display: block;   /* To prevent potential inline spacing issues */
  }

.imageBox
{
    display: flex;
    justify-content: center;
}