/************************************
 * Container + Header Styling
 ************************************/

 .logo-carousel-wrapper {
    width: 100%;
    background-color: #fff;
    padding: 20px 0;
    text-align: center; /* Center align the text for title/subtitle */
  }
  
  .logo-carousel-header {
    margin-bottom: 10px;
  }
  
  .logo-carousel-header h2 {
    margin: 0;
    font-size: 1.5rem; /* Adjust as needed */
    font-weight: 600;
  }
  
  .logo-carousel-header p {
    margin: 0;
    font-size: 1rem; /* Adjust as needed */
    color: #777;
  }
  
  /************************************
   * Carousel Container
   ************************************/
  
  .logo-carousel-container {
    display: flex;
    align-items: center;
    position: relative;
    margin: 0 auto;
    max-width: 1200px; /* Limit max width if desired */
    padding: 0 20px;
  }
  
  .logo-carousel {
    display: flex;
    overflow-x: hidden;
    position: relative;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    gap: 10px;
    scroll-behavior: smooth;
    width: 100%;
    padding: 10px 0;
  }
  
  .logo-carousel::-webkit-scrollbar {
    display: none; /* Hide scrollbar on WebKit */
  }
  
  .logo-carousel {
    -ms-overflow-style: none; /* IE, Edge */
    scrollbar-width: none; /* Firefox */
  }
  
  /************************************
   * Logo Items
   ************************************/
  
   .logo-item {
    flex: 0 0 auto;
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    transition: transform 0.3s ease;
    padding: 10px;
  }
  
  .logo-item:hover {
    transform: scale(1.05);
  }
  .logo-item.preview {
    opacity: 0.5;
  }
  .logo-item img {
    max-width: 100%;
    max-height: 100%;
    display: block;
  }
  
  /************************************
   * Carousel Buttons
   ************************************/
  
  .carousel-button {
    background: none;
    border: none;
    font-size: 1.5em;
    cursor: pointer;
    color: #555;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 10px;
    z-index: 10;
    transition: transform 0.2s ease, color 0.2s ease;
  }
  
  .carousel-button:hover {
    color: #333;
  }
  
  /* Button press animation */
  .carousel-button:active {
    transform: scale(0.9) translateY(-50%);
  }
  
  .carousel-button-left {
    left: 0;
  }
  
  .carousel-button-right {
    right: 0;
  }
.centerFlex
{
    display: flex;
    justify-content: center;
    font-size: 2rem;
}  