.longText {
    font-family: 'Noto Sans', sans-serif;
    font-style: normal;
    font-weight: 550;
    line-height: 1.6;
    /* Added for better readability */
}

.textBox {
    max-width: 1140px;
    margin: 0 auto;
    /* Added to center the textBox */
    padding: 20px;
    /* Added padding for better spacing on smaller screens */
    text-align: justify;
}

.paraHeading {
    font-family: 'Noto Sans', Sans-Serif;
    font-weight: 700;
    font-size: 35px;
    line-height: 1.5;
}

.paraImage
{
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* position: absolute; */
    top: 0;
    left: 0;
}