.gallery-container {
    width: 100%;
    padding: 15px;
    display: flex; /* Ensure it takes full width */
    justify-content: center; /* Center the gallery horizontally */
}

.gallery-container-company {
    width: 100%;
    padding: 15px;
    display: flex; /* Ensure it takes full width */
    justify-content: center; /* Center the gallery horizontally */
}

.gallery-container-products {
    width: 100%;
    padding: 15px;
    display: flex; /* Ensure it takes full width */
    justify-content: center; /* Center the gallery horizontally */
}

.bdt-custom-gallery {
    padding-right: 20px;
    display: grid; /* Use grid layout */
    grid-template-columns: repeat(6, minmax(0, 1fr)); /* 6 columns */
    gap: 15px;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.5s ease-out, transform 0.5s ease-out;
    /* max-width: 1200px; Limit width for larger screens */
    width: 95%; /* Take full width within container */
}
.bdt-custom-gallery-company {
    padding-right: 20px;
    display: grid; /* Use grid layout */
    grid-template-columns: repeat(4, minmax(0, 1fr)); /* 6 columns */
    gap: 50px;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.5s ease-out, transform 0.5s ease-out;
    /* max-width: 1200px; Limit width for larger screens */
    width: 70%; /* Take full width within container */
}

.bdt-custom-gallery-products {
    padding-right: 20px;
    display: grid; /* Use grid layout */
    grid-template-columns: repeat(4, minmax(0, 3fr)); /* 6 columns */
    gap: 20px;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.5s ease-out, transform 0.5s ease-out;
    /* max-width: 1200px; Limit width for larger screens */
    width: 80%; /* Take full width within container */
}

.bdt-custom-gallery.slide-in {
    opacity: 1;
    transform: translateY(0);
}
.bdt-custom-gallery-company.slide-in {
    opacity: 1;
    transform: translateY(0);
}
.bdt-custom-gallery-products.slide-in {
    opacity: 1;
    transform: translateY(0);
}

.bdt-gallery-item {
    position: relative;
    overflow: hidden;
    min-height: 205px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.bdt-gallery-thumbnail {
    position: relative;
    cursor: pointer; /* Add cursor pointer to thumbnail */
}

.bdt-gallery-thumbnail img {
    display: block;
    width: 100%;
    height: auto;
    transition: transform 0.3s ease;
}

.bdt-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    background-color: rgba(0, 0, 0, 0.5);
}

.bdt-gallery-thumbnail:hover .bdt-overlay {
    opacity: 1;
}

.bdt-gallery-item-link-wrapper {
    color: white;
    font-size: 24px;
    cursor: pointer;
}

.ep-icon-plus-circle {
    font-size: 3em;
}

.image-viewer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
}

.enlarged-image {
    max-width: 90%;
    max-height: 90%;
}

/* Responsive Columns (for smaller screens, adjust grid) */
@media (max-width: 960px) { /* Below desktop size */
    .bdt-custom-gallery {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Responsive columns */
    }
    .bdt-custom-gallery-company {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Responsive columns */
    }
    .bdt-custom-gallery-products {
        grid-template-columns: repeat(auto-fit, minmax(200px, 3fr)); /* Responsive columns */
    }
}

.objtext
{
    font-family: 'Noto Sans', Sans-Serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.5;
}
@media (max-width: 640px) { /* Mobile */
    .bdt-custom-gallery {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* More columns on mobile if needed */
    }
    .bdt-custom-gallery-company {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* More columns on mobile if needed */
    }
    .bdt-custom-gallery-products {
        grid-template-columns: repeat(auto-fit, minmax(150px, 3fr)); /* More columns on mobile if needed */
    }
}