.bar-chart-section {
    --container-widget-width: 100%;
    --e-container-column-width: 100%;
    width: var(--container-widget-width);
    max-width: var(--e-container-column-width);
    --container-widget-height: auto;
    --e-container-column-height: auto;
    height: var(--container-widget-height);
    background-color: #f9f9f9;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center content horizontally */
    align-items: center; /* Center items vertically in flex container */
    background-image: url('https://electrech.com/wp-content/uploads/2024/10/Phaco-Corneal-Refractive-Surgery-Squint-Ptosis-Endoscopic-DCR-Lid-Reconstruction-Lasers-For-Diabetes-Eye-Disease.png');
    background-size: cover;
    background-position: center;
    color: white; /* Adjust text color for better visibility on background */
}

.e-con-inner {
    display: flex;
    flex-wrap: wrap; /* Enable wrapping for responsiveness */
    justify-content: center; /* Center items inside e-con-inner */
    max-width: 1100px; /* Max width for the content area, twice of 550px */
    width: 100%; /* Ensure it takes full width within parent */
}

.text-container {
    width: 50%; /* Initial width for text container */
    max-width: 550px; /* Max width for text */
    padding: 20px;
    box-sizing: border-box; /* Include padding in width calculation */
    --e-column-flex-grow: 1;
    display: flex;
    justify-content: center; /* Center text content */
}

.chart-container {
    width: 50%; /* Initial width for chart container */
    max-width: 550px; /* Max width for chart */
    padding: 20px;
    box-sizing: border-box; /* Include padding in width calculation */
    --e-column-flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center chart content vertically */
}

.elementor-element-0e3ad37 .elementor-widget-container {
    font-size: 16px;
    line-height: 1.6;
    color: white; /* Adjusted text color to white for background */
    text-align: left; /* Align text to the left within its container */
}

.elementor-element-33e4589,
.elementor-element-3d4901d,
.elementor-element-28fa9a5,
.elementor-element-96262b6,
.elementor-element-fa809ff,
.elementor-element-159e53b {
    margin-bottom: 10px;
    width: 100%; /* Make bars take full width of chart container */
}


.elementor-progress-wrapper {
    background-color: rgba(224, 224, 224, 0.5); /* Semi-transparent background */
    border-radius: 5px;
    overflow: hidden;
}

.elementor-progress-bar {
    background-color: #6c757d;
    color: #fff;
    padding: 10px 15px;
    text-align: left;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: width 1s ease-out;
    width: 0%;
    min-width: 20px;
}

.elementor-progress-text {
    white-space: nowrap;
}

.elementor-progress-percentage {
    white-space: nowrap;
    margin-left: 15px;
}

.blackText
{
    color: black;
    font-weight: 400;
    font-family: 'Noto Sans', sans-serif;
}
/* Responsive behavior - Stack text and chart on smaller screens */
@media (max-width: 768px) {
    .text-container,
    .chart-container {
        width: 100%; /* Full width when stacked */
        padding: 20px; /* Keep padding */
    }

    .e-con-inner {
        flex-direction: column; /* Stack items vertically on smaller screens */
        align-items: center; /* Center items when stacked */
    }
    .bar-chart-section
    {
        padding: 10px;
    }
}