/************************************
 * Container + Header Styling
 ************************************/
 .logo-carousel-wrapper-v2 {
  width: 100%;
  background-color: #fff;
  padding: 20px 0;
  overflow-y: hidden;
  text-align: center;
}

.logo-carousel-header-v2 {
  margin-bottom: 10px;
}

.logo-carousel-header-v2 h2 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.logo-carousel-header-v2 p {
  margin: 0;
  font-size: 1rem;
  color: #777;
}

/************************************
 * Carousel Container
 ************************************/
.logo-carousel-container-v2 {
  display: flex;
  align-items: center;
  position: relative;
  margin: 0 auto;
  width: 100%;
  padding: 0;
  overflow: hidden;
}

.logo-carousel-v2 {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  gap: 0;
  scroll-behavior: smooth;
  width: 100%;
  height: 370px;
  padding: 0;
}

.logo-carousel-v2::-webkit-scrollbar {
  display: none;
}

.logo-carousel-v2 {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

/************************************
 * Logo Items
 ************************************/
.logo-item-v2 {
  flex: 0 0 auto;
  width: calc(100% / 5); /* Default: 5 items per row */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease;
  overflow: hidden;
}

.logo-item-v2:hover {
  transform: scale(1.05);
}

.logo-item-v2 img {
  max-width: 100%;
  max-height: 100%;
  display: block;
  object-fit: cover;
  height: 100%;
  width: 100%;
}

/************************************
 * Carousel Buttons
 ************************************/
.carousel-button-v2 {
  background: none;
  border: none;
  font-size: 2em;
  cursor: pointer;
  color: white;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 10px;
  z-index: 10;
  transition: transform 0.2s ease, color 0.2s ease;
  opacity: 0.7;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

.carousel-button-v2:hover {
  color: white;
  opacity: 1;
}

.carousel-button-v2:active {
  transform: scale(0.95) translateY(-50%);
}

.carousel-button-left-v2 {
  left: 10px;
}

.carousel-button-right-v2 {
  right: 10px;
}

.centerFlex {
  display: flex;
  justify-content: center;
  font-size: 2rem;
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .logo-item-v2 {
    width: calc(100% / 4);
  }
}

@media (max-width: 992px) {
  .logo-item-v2 {
    width: calc(100% / 3);
  }
}

@media (max-width: 768px) {
  .logo-item-v2 {
    width: calc(100% / 2);
  }
  .logo-carousel-v2 {
    height: 250px;
  }
  .logo-carousel-header-v2 h2 {
    font-size: 1.3rem;
  }
  .logo-carousel-header-v2 p {
    font-size: 0.9rem;
  }
}

@media (max-width: 576px) {
  .logo-item-v2 {
    width: 100%;
  }
  .logo-carousel-v2 {
    height: 200px;
  }
}
