.cable-cont-container {
    padding-bottom: 30px;
}

.cable-cont-section {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-transition: 0.3s;
    widgets-spacing: 20px 20px;
    max-width: 100%;
    text-align: start;
    gap: 0px;
    flex-flow: column nowrap;
    place-content: normal;
    align-items: normal;
    position: relative;
    width: 100%;
    min-width: 0px;
    min-height: 0px;
    height: auto;
    border-radius: 0px;
    z-index: auto;
    overflow: visible;
    transition: background 0.3s, border 0.3s, box-shadow 0.3s, transform 0.4s;
    margin-block-start: 0px;
    margin-inline-end: auto;
    margin-block-end: 0px;
    margin-inline-start: 0px;
    padding-inline-start: 10px;
    padding-inline-end: 10px;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 1;
    order: 0;
    align-self: auto;
    margin-bottom: 0px; /* Resetting inline style margin-bottom */
    padding-top: 10px; /* Adding padding-top for content spacing */
    padding-bottom: 10px; /* Adding padding-bottom for content spacing */
}

.cable-cont-section.last-cable-type {
    margin-bottom: 50px; /* Applying margin-bottom to the last cable type section */
}

.cable-cont-content {
    box-sizing: border-box;
    gap: 0px;
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding-inline: 0px;
    flex-wrap: nowrap;
    justify-content: normal;
    align-items: normal;
    align-content: normal;
    flex: 1 auto auto;
    align-self: auto;
    display: flex;
    flex-direction: column;
    text-align: start;
    padding-block-start: 10px;
    padding-block-end: 10px;
}


.cable-cont-text-widget {
    box-sizing: border-box;
    /* widgets-spacing: 20px 20px; */
    max-width: 100%;
    min-width: 0px;
    margin-block-end: 0px;
    position: relative;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: normal;
    align-items: normal;
    align-content: normal;
    gap: 0px;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 1;
    order: 0;
    align-self: auto;
}

.cable-cont-text-wrapper {
    box-sizing: border-box;
    transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s, transform 0.4s;
}

.cable-cont-text-wrapper h3 {
    margin-block-end: 19px;
    font-family: "Noto Sans", sans-serif;
    font-size: 30px;
    font-weight: 700;
    line-height: 45px;
    color: rgb(40, 41, 42);
}

.cable-cont-text-wrapper h4 {
    margin-block-end: 17.5px;
    font-family: "Noto Sans", sans-serif;
    font-size: 25px;
    font-weight: 700;
    line-height: 37.5px;
    color: rgb(40, 41, 42);
}


.cable-cont-text-wrapper p,
.cable-cont-text-wrapper ul {
    font-family: "Noto Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    margin-block-end: 24px;
    margin-bottom: 0px; /* For ul reset margin-bottom from inline style */
    padding-left: 0; /* For ul reset padding-left from inline style */
    list-style-position: inside; /* For ul to keep list markers inside */
}

.cable-cont-text-wrapper ul {
    padding-inline-start: 40px; /* Re-apply list indent for ul */
}

.cable-cont-image-gallery-widget {
    box-sizing: border-box;
    /* widgets-spacing: 20px 20px; */
    max-width: 100%;
    min-width: 0px;
    margin-block-end: 0px;
    position: relative;
    flex-direction: row;
    flex-wrap: wrap; /* Allow images to wrap on smaller screens */
    justify-content: flex-start; /* Align images to the start */
    align-items: flex-start;
    align-content: flex-start;
    gap: 0px; /* Spacing between images - set to 0 to remove gap */
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 1;
    order: 0;
    align-self: auto;
}

.cable-cont-gallery {
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start; /* Align images to the start */
    margin-left: 0px; /* Removed negative margins */
    margin-right: 0px; /* Removed negative margins */
}

.cable-cont-gallery-item {
    box-sizing: border-box;
    max-width: 25%; /* Default 4 columns */
    width: 265px;
    display: flex; /* Enable flexbox for item */
    flex-direction: column; /* Stack content vertically */
    margin-bottom: 0px; /* Remove margin-bottom */
    padding: 0px; /* Remove padding to eliminate gap */
    text-align: center;
    vertical-align: top;
    cursor: pointer;
}

.cable-cont-gallery-item-wrapper {
    box-sizing: border-box;
    padding: 0px; /* Add padding inside wrapper for spacing between images */
}

.cable-cont-gallery-item img {
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    box-sizing: border-box;
    display: block;
    margin: 0 auto; /* Center image in its container */
    border-radius: 0px;
    box-shadow: none;
}

/* Media Queries for Responsiveness */

@media screen and (max-width: 1200px) {
    .cable-cont-content {
        max-width: 960px; /* Adjust content max-width for larger tablets/smaller desktops */
    }
}


@media screen and (max-width: 992px) { /* Tablets */
    .cable-cont-content {
        max-width: 720px; /* Adjust content max-width for tablets */
    }
    .cable-cont-gallery-item {
        max-width: 265px; /* 3 columns on tablets */
    }
    .cable-cont-text-wrapper h3 {
        font-size: 28px;
        line-height: 42px;
    }

    .cable-cont-text-wrapper h4 {
        font-size: 23px;
        line-height: 34.5px;
    }
}

@media screen and (max-width: 768px) { /* Mobile */
    .cable-cont-content {
        max-width: 540px; /* Adjust content max-width for mobiles */
    }
    .cable-cont-gallery-item {
        max-width: 265px; /* 2 columns on smaller mobiles */
    }
    .cable-cont-text-wrapper h3 {
        font-size: 24px;
        line-height: 36px;
    }
    .cable-cont-text-wrapper h4 {
        font-size: 20px;
        line-height: 30px;
    }
}


@media screen and (max-width: 576px) { /* Smaller Mobiles */
    .cable-cont-content {
        max-width: 100%; /* Full width on very small screens */
        padding-left: 15px;
        padding-right: 15px;
    }
    .cable-cont-gallery-item {
        max-width: 265px; /* 1 column on smallest mobiles */
    }
    .cable-cont-text-wrapper h3 {
        font-size: 22px;
        line-height: 33px;
    }
    .cable-cont-text-wrapper h4 {
        font-size: 18px;
        line-height: 27px;
    }
}



