.cable-cont-gallery {
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Align images to the start */
}

.cable-cont-gallery-item {
    box-sizing: border-box;
    max-width: 265px; /* Default 4 columns */
    width: 100%;
    display: flex; /* Enable flexbox for item */
    flex-direction: column; /* Stack content vertically */
    margin-bottom: 0px; /* Remove margin-bottom */
    padding: 0px; /* Remove padding from item */
    text-align: center;
    vertical-align: top;
    cursor: pointer;
}

/* ... Media Queries (no changes needed here for gap removal) ... */

/* Lightbox Styles */
.lightbox-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8); /* Semi-transparent black overlay */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure it's on top of everything */
    cursor: pointer; /* Indicate clickable overlay to close */
}

.lightbox-content {
    max-width: 90%; /* Adjust as needed */
    max-height: 90%; /* Adjust as needed */
    position: relative; /* For close button positioning if needed */
}

.lightbox-image {
    display: block;
    max-width: 100%;
    max-height: 100%;
    object-fit: contain; /* Make image fit inside container */
}