.slider-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  /* Height set in media queries */
}

.image-wrapper {
  display: flex;
  transition: transform 0.5s ease-in-out;
  height: 100%; /* Take full height of container */
}

.slider-image {
  flex: 0 0 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Media queries (keep existing breakpoints) */
@media (min-width: 768px) {
  .slider-container { height: 640px; }
}

@media (max-width: 768px) {
  .slider-container { height: 290px; }
}


/* Arrow button styles */
.slider-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: white;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  padding: 10px;
  z-index: 1;
}

.slider-arrow.left {
  left: 10px;
}

.slider-arrow.right {
  right: 10px;
}

/* Ensuring the arrows are visible over the image */
.slider-arrow svg {
  fill: white;
}
