/* home.css */

.home-section {
    display: flex;
    align-items: center;
    padding: 20px;
    max-width: 1200px;
    margin: 20px auto;
    /* justify-content: space-around;  Removing space-around to make text and image closer */
    /* gap: 20px; Removing gap as margin will handle spacing */
}

.text-content {
    flex: 1;
    /* padding-right: 20px; Removing padding-right as margin-right will handle spacing */
    margin-right: 3vw; /* Adding 3vw margin to the right of text content */
}

.section-title {
    font-size: 2em;
    font-weight: bold;
    margin-bottom: 10px;
    color: #000;
}

.underline {
    height: 3px;
    width: 80px;
    background-color: #000;
    margin-bottom: 15px;
}

.section-paragraph {
    font-size: 1em;
    line-height: 1.6;
    color: #333;
    text-align: justify;
}

.image-container {
    flex: 1;
    text-align: right;
}

.section-image {
    max-width: 422px; /* Set maximum width to 422px */
    width: 90%;
    height: auto;
    border-radius: 5px;
    display: block;
    margin-left: auto;
}

/* Media query for smaller screens (e.g., mobile devices) */
@media (max-width: 768px) {
    .home-section {
        flex-direction: column; /* Stack text and image vertically */
        text-align: center; /* Center text content on smaller screens */
        /* justify-content: space-around; Removing justify-content for stacked layout */
    }

    .text-content {
        padding-right: 0;
        margin-bottom: 20px;
        text-align: center;
        margin-right: 0; /* Remove right margin on smaller screens */
    }

    .image-container {
        text-align: center;
    }

    .section-image {
        margin-left: auto;
        margin-right: auto;
    }

    .section-title {
        font-size: 1.8em;
    }
    .section-paragraph {
        text-align: justify;
    }
}


.GoogleMap
{
    height: 518px;
    width: 90%;
}

.h1-heading
{
    font-family: "Noto Sans", Sans-serif;
