/* navbar.css */
.container {
  max-width: 1200px;
  margin: 0 10%;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-nav {
  background-color: white;
  padding: 20px 0;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.logo-container img {
  height: 80px;
}

.nav-links-container {
  display: flex;
  align-items: center;
  justify-content: flex-end; /* Right align nav links */
  flex: 1; /* Take remaining space */
}

.nav-links {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end; /* Right align list items */
}

.nav-links li {
  margin-left: 20px; /* Changed to margin-left */
}

.nav-link {
  text-decoration: none;
  font-weight: bold;
  color: black;
  transition: color 0.3s ease;
  white-space: nowrap;
}

.nav-link:hover {
  color: #11C4F3;
}

.nav-link.active {
  color: #11C4F3;
}


/* Hamburger Menu Styles */
.hamburger-icon {
  background: none;
  border: none;
  color: black;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 10px;
  display: none;
  transition: transform 0.3s ease; /* Add transition for smooth animation */
}

.hamburger-icon.animating {
    transform: rotate(90deg); /* Example animation */
}


.hamburger-menu {
  display: none;
  position: fixed; /* Changed to fixed to cover full viewport */
  top: 0;
  right: 0;
  width: 300px; /* Adjust width as needed */
  height: 100%; /* Full height */
  background-color: #004198; /* Blue background */
  color: white; /* White text */
  box-shadow: -2px 0px 5px rgba(0,0,0,0.2);
  z-index: 100;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;
  transition: transform 0.3s ease-in-out; /* For slide animation */
  transform: translateX(100%); /* Initially hidden off-screen */
}

.hamburger-menu.open {
  display: flex;
  transform: translateX(0); /* Slide in when open */
}

.hamburger-menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}

.hamburger-logo img {
  height: 60px; /* Adjust logo size in hamburger menu */
}

.close-icon {
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 10px;
}


.hamburger-nav-links { /* Style for nav links inside hamburger */
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column; /* Stack links vertically */
  align-items: flex-start; /* Align to the start */
  width: 100%; /* Take full width of the menu */
}

.hamburger-nav-links li {
  margin-right: 0;
  margin-bottom: 15px;
  width: 100%; /* Full width for list items */
}

.hamburger-nav-links li:last-child {
  margin-bottom: 0;
}

.hamburger-nav-links .nav-link { /* Style nav links inside hamburger */
  color: white; /* White color for links in hamburger */
  display: block; /* Make links block level for full width click area */
  padding: 10px 0; /* Add some vertical padding */
  width: 100%;      /* Make links take full width */
}
.hamburger-nav-links .nav-link.active {
  color: #11C4F3; /* Active tab color in hamburger */
}
.hamburger-nav-links .nav-link:hover {
  color: #11C4F3; /* Hover color in hamburger */
}


/* Media Query for smaller screens */
@media (max-width: 1199px) { /* Changed to 1199px to trigger at 1200px */
  .nav-links {
    display: flex !important;
  }
  .nav-links-container {
    overflow: hidden;
  }
  .hamburger-icon {
    display: block;
  }

  .nav-links {
      display: none;
  }

  .hamburger-menu.open {
      display: flex;
  }

  .nav-links-container {
      justify-content: flex-end;
  }


}