.h1-heading-container {
    position: relative;
    display: block; /* Take full width */
    width: 100%;
    overflow: hidden; /* Prevent horizontal scroll */
    text-align: center; /* Ensure content is centered */
}

.h1-heading {
    font-family: "Noto Sans", Sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 3rem;
    color: #2d3748;
    position: relative;
    z-index: 1;
    white-space: nowrap;
    overflow: hidden;

    text-align: center; /* Ensure text itself is centered within h1 */
}

.h1-heading-shadow {
    font-family: "Noto Sans", Sans-serif;
    font-style: normal;
    font-weight: 600; /* Bolder shadow */
    font-size: 3.2rem;
    letter-spacing: 0.4rem;
    color: #a0aec081;
    position: absolute;
    top: 0;
    left: 50%; /* Center it horizontally */
    z-index: 0;
    opacity: 0.6; /* Initial opacity */
    transform: translate(-50%, -16px);
    white-space: nowrap;
    text-align: center; /* Ensure shadow text is also centered if needed */
}

.h1-subheading {
    font-family: "Noto Sans", Sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.5rem;
    color: #4a5568;
}

@media (max-width: 999px)
{
    .h1-heading-shadow
    {
        display: none;
    }
    .h1-heading
    {
        white-space: wrap;
    }
}

@media (max-width: 500px)
{
    .h1-heading{
        font-size: 2.5rem;
    }
}