/* DividerModule.css */

.dividerContainer {
    padding: 10px;
    margin: 20px 0;
    /* border-radius: 5px; */
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden; /* Ensures no overflow if the content is bigger */
  }
  
  .dividerContainer.wrapped {
    text-align: center; /* Optional: Adjust text alignment when wrapped */
  }
  
  .underlined-text {
    text-decoration: underline;
    font-weight: bold; /* Optional: Make text bolder if desired */
    font-size: 2.5rem; /* Optional: Adjust font size */
    color: white;
  }
  